@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: 'Nunito', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.App {
  background-color: white;
  overflow: hidden; }

.contenido {
  padding: 10px; }

nav {
  padding: 0;
  margin: 0; }
  nav ul {
    padding: 0;
    margin: 0;
    display: flex;
    list-style: none;
    justify-content: center;
    align-items: center;
    background-color: #22A6B3; }
  nav a {
    padding: 10px 25px;
    display: block;
    text-align: center;
    text-decoration: none;
    color: white;
    font-weight: 400;
    -webkit-transition: all 0.2s ease 0s;
    transition: all 0.2s ease 0s; }
    nav a:hover {
      background-color: white;
      color: #22A6B3; }
    nav a:active {
      background-color: #22A6B3;
      color: white;
      border: 0; }
  nav button {
    display: none; }
  @media screen and (max-width: 768px) {
    nav ul {
      display: none; }
    nav button {
      display: block;
      background-color: #22A6B3;
      color: white;
      border: 1px solid lightgray;
      -webkit-transition: all 0.2s ease 0s;
      transition: all 0.2s ease 0s;
      width: 100%;
      height: 40px; }
      nav button:focus {
        outline: 0; }
      nav button:active {
        background-color: white;
        color: #22A6B3; }
      nav button:hover {
        cursor: pointer; } }

header {
  padding: 10px 5px;
  margin: 0;
  height: 50px;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  header div {
    margin: 5px; }
  header img {
    max-width: 300px; }
  header .searchInput {
    max-width: 500px;
    width: 100%;
    float: right;
    display: flex; }
    header .searchInput input {
      width: 100%;
      border-top: 1px solid lightgray;
      border-left: 1px solid lightgray;
      border-bottom: 1px solid lightgray;
      border-right: 0;
      height: 25px;
      padding: 5px;
      color: gray; }
      header .searchInput input:focus {
        outline: 0; }
    header .searchInput button {
      background-color: #22A6B3;
      color: white;
      border: 1px solid lightgray;
      -webkit-transition: all 0.2s ease 0s;
      transition: all 0.2s ease 0s;
      width: 40px; }
      header .searchInput button:focus {
        outline: 0; }
      header .searchInput button:active {
        background-color: white;
        color: #22A6B3; }
      header .searchInput button:hover {
        cursor: pointer; }
  header .buttons {
    display: flex; }
    header .buttons button {
      background-color: white;
      color: #252525;
      border: 0;
      width: 125px;
      height: 50px;
      font-size: 15px;
      -webkit-transition: all 0.2s ease 0s;
      transition: all 0.2s ease 0s; }
      header .buttons button:focus {
        outline: 0; }
      header .buttons button:active {
        background-color: gainsboro;
        color: black;
        border: 0; }
      header .buttons button:hover {
        cursor: pointer; }
  @media screen and (max-width: 768px) {
    header {
      text-align: center; }
      header .logo {
        width: 100%; }
      header .searchInput {
        display: none; }
      header .buttons {
        display: none; } }

.Product {
  color: #505050;
  background-color: white;
  border: 1px solid lightgray;
  text-align: center;
  vertical-align: middle;
  padding: 10px;
  justify-self: center;
  width: 200px;
  cursor: pointer; }
  .Product .Product-Image {
    width: 100%;
    height: 200px;
    position: relative; }
    .Product .Product-Image img {
      max-width: 100%;
      max-height: 100%;
      width: auto;
      height: auto;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto; }
  .Product .Product-Desc {
    overflow: hidden;
    white-space: nowrap; }
    .Product .Product-Desc h2, .Product .Product-Desc h4 {
      margin: 10px; }
  .Product .Product-Buttons .product-add {
    background-color: #22A6B3;
    color: white;
    border: none;
    width: 100px;
    height: 25px;
    margin: 10px; }
    .Product .Product-Buttons .product-add:focus {
      outline: 0; }
    .Product .Product-Buttons .product-add:active {
      background-color: white;
      color: #22A6B3; }
    .Product .Product-Buttons .product-add:hover {
      cursor: pointer; }
  .Product .Product-Buttons .product-favourite, .Product .Product-Buttons .product-compare {
    background-color: white;
    color: gray;
    border: 1px solid gray;
    width: 25px;
    height: 25px; }
    .Product .Product-Buttons .product-favourite:focus, .Product .Product-Buttons .product-compare:focus {
      outline: 0; }
    .Product .Product-Buttons .product-favourite:active, .Product .Product-Buttons .product-compare:active {
      border-color: #22A6B3;
      color: #22A6B3; }
    .Product .Product-Buttons .product-favourite:hover, .Product .Product-Buttons .product-compare:hover {
      cursor: pointer; }

.ProductsContainer {
  margin: 10px; }
  .ProductsContainer .ProductsContainer-Desc {
    margin: 20px 10px;
    color: gray;
    overflow: hidden; }
    .ProductsContainer .ProductsContainer-Desc:after {
      content: "";
      display: inline-block;
      height: 0.5em;
      vertical-align: bottom;
      width: 100%;
      margin-right: -100%;
      margin-left: 10px;
      border-top: 1px solid gray; }
  .ProductsContainer .ProductsContainer-List {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, 250px);
    grid-gap: 5px 5px;
    gap: 5px 5px;
    justify-content: center; }

footer {
  text-align: center;
  width: 100%;
  height: 50px;
  line-height: 50px;
  background-color: lightgray;
  color: gray; }

